.text-content {
  position: relative;
  font-size: 16px;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: -0.141px;
  p {
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  ol {
    list-style: none;
    padding: 0;
    counter-reset: myCounter;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 15px;
      position: relative;
      padding-left: 35px;
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        counter-increment: myCounter;
        content: counter(myCounter) " _";
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
  ul {
    list-style: none;
    padding: 0;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 10px;
      position: relative;
      padding-left: 35px;
      &::before {
        content: "";
        position: absolute;
        left: 4px;
        top: 12px;
        width: 11px;
        height: 2px;
        background: #000;
        opacity: 0.2;
      }
      &::after {
        content: "";
        position: absolute;
        left: 8px;
        top: 9px;
        width: 8px;
        height: 8px;
        border-bottom: 2px solid #000;
        border-right: 2px solid #000;
        transform: rotate(-45deg);
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  iframe {
    border: 0;
    max-width: 100%;
    vertical-align: top;
    margin: 0 0 40px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @include response(767px) {
      width: 100%;
      height: 300px;
    }
  }
  h1 {
    font-weight: 600;
    font-size: 36px;
    line-height: 135%;
    color: #011a28;
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 26px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h2 {
    font-weight: 600;
    font-size: 31px;
    line-height: 135%;
    color: #011a28;
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 22px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h3 {
    font-weight: 600;
    font-size: 27px;
    line-height: 150%;
    color: #011a28;
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      font-size: 20px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  h4 {
    font-weight: 600;
    font-size: 23px;
    line-height: 175%;
    color: #011a28;
    margin: 0 0 40px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    &:first-child {
      margin-top: 0;
    }
  }
  img {
    display: inline-block;
    vertical-align: top;
    margin: 10px 0 20px;
    &:first-child {
      margin-top: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: #000;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  table {
    margin: 0 0 40px;
    border-collapse: collapse;
    &:last-child {
      margin-bottom: 0;
    }
    thead {
      tr {
        td {
        }
      }
    }
    tr {
    }
    td {
    }
  }
}
